<template>
  <div class="selectDepartment">
    <DepartmentTab :tabData="tabData"/>
  </div>
</template>

<script>
import DepartmentTab from './departmentTab'
import store from '@/vuex/index'
export default {
  name: 'selectDeparment',
  data () {
    return {
      tabData: []
    }
  },
  components: {
    DepartmentTab
  },
  methods: {},
  created () {
    const {hospitalId, hospitalName} = this.$route.params
    this.$store.commit(
      'gmodule/changeTitle',
      this.$route.params.hospitalName
    )
    store.commit('wmodule/changeHospital', {hospitalId: hospitalId, hospitalName: hospitalName})
  }
}
</script>

<style scoped lang="less">

</style>



