<template>
  <div class="departmentTab">
    <ul class="tab" ref="tabBox">
      <li
        class="tabItem"
        v-for="item in deptList"
        :class="{active:item.index===1}"
        @click="tabFn(item.index)"
        :key="item.index"
        ref="tabItem"
      >
        <i></i>
        <span>{{item.deptType}}</span>
      </li>
    </ul>
    <div class="tabList">
      <ul class="list" :class="{active:item.index===1}" :key="item.index" v-for="item in deptList" ref="listItem">
        
        <router-link
          class="listItem"
          v-for="listItem in item.list"
          :key="listItem.index"
          tag="li"
          :to="{name:'departmentDetails',params:{deptId:listItem.hospitalDept.id,deptName:listItem.hospitalDept.deptName}}">
          {{listItem.hospitalDept.deptName}}</router-link>
      </ul>
    </div>
  </div>
</template>

<script>
import store from "@/vuex/index";

export default {
  data() {
    return {
    };
  },
  methods: {
    tabFn(itemId) {
      const tabList = this.$refs.tabItem; //获取所有的tab标签
      const ItemList = this.$refs.listItem; //tab下所有的list
      tabList.forEach((tabItem, tabIndex) => {
        if (tabIndex + 1 === itemId) {
          tabItem.className += " active";
          ItemList.forEach((listItem, listIndex) => {
            if (itemId === listIndex + 1) {
              listItem.className += " active";
            } else {
              listItem.className = "list";
            }
          });
        } else {
          tabItem.className = "tabItem";
        }
      });
    }
  },
  computed: {
    deptList() {
      return store.state.wmodule.curDeptList;
    },
    hospitalId() {
      return store.state.wmodule.curHospitalId || this.$route.params.hospitalId;
    },
    hospitalName() {
      return (
        store.state.wmodule.curHospitalName || this.$route.params.hospitalName
      );
    }
  },
  created() {
    const { curHospitalId, curHospitalName } = store.state.wmodule;
    if (!curHospitalId) {
      store.commit("wmodule/changeHospital", {
        hospitalId: this.$route.params.hospitalId,
        hospitalName: this.$route.params.hospitalName
      });
    }
    console.log("当前医院id=>", this.hospitalId);
    store.dispatch("wmodule/getDeptByHospitalId", this.hospitalId);
  },
  mounted() {
    // 根据设备的屏幕长度来动态计算选项卡列表的高度(tabBox)
    console.log("当前设备屏幕高度", document.documentElement.clientHeight);
    //this.$refs.tabBox.style.height = (document.documentElement.clientHeight - 44 - 45) + "px";// 头部44px 底部48px 为了达到底部不露白所以少减3px
  }
};
</script>

<style scoped lang="less">
.departmentTab {
  width: 100%;
  overflow: hidden;
  padding-bottom: 0.49*2rem;
  display: flex;

  .tab {
    float: left;
    height: auto;
    background: #f1f1f1;
    overflow: hidden;
    position: relative;

    .tabItem {
      box-sizing: border-box;
      width: 1.4*2rem;
      height: 0.44*2rem;
      line-height: 0.44*2rem;
      padding-left: 0.197*2rem;
      border-bottom: 1px solid #f6f7f8;

      i {
        display: inline-block;
        width: 0.14*2rem;
        height: 0.14*2rem;
        background: url("../../../assets/appointment/office_icon_pre@2x.png")
          center no-repeat;
        background-size: 0.14*2rem 0.14*2rem;
      }

      span {
        margin-left: 0.09*2rem;
        font-family: PingFangSC-Regular, sans-serif;
        font-size: 0.14*2rem;
        line-height: 0.2*2rem;
        color: #272727;
      }

      &.active {
        background: #fff;

        i {
          background: url("../../../assets/appointment/office_icon@2x.png")
            center no-repeat;
          background-size: 0.14*2rem 0.14*2rem;
        }

        span {
          color: #3391e8;
        }
      }
    }
  }

  .tabList {
    flex: 1;
    width: 2.43*2rem;
    box-sizing: border-box;
    padding-left: 0.1*2rem;
    position: relative;

    .list {
      width: 100%;
      position: absolute;
      display: none;
      padding-bottom: 0.44*2rem;
      overflow-y: auto;
      height: 100%;

      .listItem {
        width: 100%;
        height: 0.44*2rem;
        font-size: 0.14*2rem;
        font-family: PingFangSC-Regular, sans-serif;
        line-height: 0.44*2rem;
        border-bottom: 1px solid #f6f7f8;
      }

      &.active {
        display: block;
      }
    }
  }
}
</style>



